<template>
  <div class="icon-view">
    <i :class="name"></i>
    <div class="info">
      <div class="part name">
        <i class="dc-clipboard-solid icon">
          <span>
            &nbsp;&nbsp;{{ iconname }}
          </span>
        </i>
      </div>
      <br>
      <div class="part icon-name">
        <i class="dc-eye-solid icon">
          <span>&nbsp;&nbsp;{{ name }}</span>
        </i>
      </div>
      <br>
      <div class="part code">
        <i class="dc-code-solid icon">
        <PlainText v-bind:text="'&nbsp;<i class=\'' + name + '\'>'"/>
        </i>
      </div>
    </div>
  </div>
</template>

<script>
import PlainText from '../components/PlainText.vue'
export default {
  name: "IconView",
  data: function() {
    return {
      items: [],
      iconname: "",
    }
  },
    props: { 
        name: String,
    }, 
    components:{
      PlainText
    },
    mounted: function() {
      fetch(`https://api.npoint.io/1d928c07f2d71aa7ed54`, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
      })
      .then((response) => response.json())
      .then((text) => {
        this.items = text
        for(let i of text.items){
          if(i.name === this.name){
            this.iconname = i.subname
            document.title = i.subname + " | Discons - Distro Icons"
            break
          }
        }
    });

    document.title = this.iconname + " | Discons - Distro Icons"
  }
}
</script>

